body{
  margin: 0;
  padding: 0;
  background-color: rgb(71, 64, 64);
  color: white;
}
.screen{
  width: 50%;
  margin: 0 auto;
  height: 92vh;
  background-color: rgb(31, 28, 28);
  overflow: auto;
  border-radius: 15px;
}
.chat{
  text-align: left
}
.input{
  margin: 0 auto;
  width: 50%;
  height: 8vh;
  display: -ms-flexbox;
  display: flex;
  
}
.input > input{
  font-size: 32px;
  border-radius: 15px;
  width: 80%;
}
.input > button{
  width: 20%;
  border-radius: 15px;
  font-size: 32px;
  color: lightseagreen;
}
.itsMe{
  text-align: right
}
.msg{
  display: inline-block;
  padding: 10px 20px;
  margin: 10px;
  border-radius: 15px;
}
.itsMe .msg{
  background-color: grey;
}
.chat .msg{
  background-color: rgb(32, 194, 40);
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
  .screen{
    width: 100%;
    border-radius: 0;
    height: 80vh;
  }
  .input{
    width: 100%;
    height: 60px;
  }
  .input > input{
    font-size: 18px;
  }
}


